import {Link} from "react-router-dom"

function Card(props) {
    return (
        <div>
            <p>{props.name}</p>
            <Link to={`/card/${props.id}`}>
                <img src={props.imageURL} alt={props.name}/> 
            </Link>
            <br/>
            <button onClick={() => props.onDelete(props.id)}>Delete</button>
        </div>
    )
}

export default Card;