import React, { useState, useEffect } from "react";

import AddCardForm from "./AddCardForm";
import Card from "./Card";

import axios from "axios";

function HomePage() {

  const [cards, setCards] = useState([]);
  const [id, setID] = useState(0);

  useEffect(() => {
    getCards();
  }, [id])

  function getCards() {
    axios.get("http://localhost:3000/initialCards").then(response => {
    setCards(response.data);
    })
  }

  let cardNum = 0;
  return (
    <div className="HomePage">
      <AddCardForm onAdd={onAddCard}/>
      {cards.map(({name, imageURL, id}) => {
        return <Card name={name} imageURL={imageURL} key={cardNum++} onDelete={onClickDelete} id={id}/>
      })}
      <br/>
      <button onClick={onClickDeleteAll}>Delete All</button>
    </div>
  );

  function onAddCard(name, imageURL, description) {
    axios.post("http://localhost:3000/addCard", 
      { name: name,
        imageURL: imageURL, 
        description: description
      }
    ).then(() => setID(id + 1));

  }

  function onClickDelete(cardID) {
    console.log(cardID);
    axios.delete(`http://localhost:3000/deleteCard/${cardID}`).then(() => setID(id + 1));
  }

  function onClickDeleteAll() {
    axios.delete(`http://localhost:3000/deleteAll`).then(() => setID(id + 1));
  }

  
  
}

export default HomePage;
